import back from '@/components/back'
export default {
    components: {
        back
    },
    data(){
        return{
            list: [],
            show: false,
            model: '',
            index: 0,
            Patient: this.$store.state.patient,
        }
    },
    computed:{
        title(){
            return `${this.Patient.PatientName}的随访计划`
        }
    },
    created(){
        this.pull()
    },
    methods: {
        pull(){
            this.$get('PlatFormAPI/Followup/QueryTaskRecord',{
                PlanId: this.$route.query.id
            }).then(o => {
                this.list = o
            })
        },
        toShow(i){
            this.idx = i
            this.show = true
        },
        toQuestion(){
            location.href = this.model.QuestionnaireUrl
        }
    }
}



